.statistics {
  margin-bottom: 50px;
  width: 300px;
  padding-top: 10px;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;

  background-color: #68759e;
  border: 1px solid #8b8b8b;
  border-radius: 10px;
}

.title {
  margin-bottom: 10px;
  text-transform: uppercase;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  color: #000000;
}
.list {
  display: flex;
  width: 100%;
  background-color: #3cb2bb;
}
